/* eslint-disable react/no-danger */
import {
  selectConfigLogo,
  selectDetailProject,
  selectSceneSelected,
  selectThemeConfigGlobal,
} from '@containers/Home/selectors';
import dynamic from 'next/dynamic';
import React, { memo } from 'react';
import { useSelector } from 'react-redux';
import { HeaderWrapper } from './styled';

const Logo = dynamic(() => import('@containers/Home/components/Logo'));

export const Header = memo(() => {
  const configLogo = useSelector(selectConfigLogo);
  const detailProject = useSelector(selectDetailProject);
  const sceneSelected = useSelector(selectSceneSelected);
  const themeConfig = useSelector(selectThemeConfigGlobal);

  const onClickLogo = () =>
    (window as any).open('https://panoee.com', '_blank');

  return (
    <HeaderWrapper className={`topInfo `}>
      {configLogo?.isEnabled ? (
        <Logo />
      ) : (
        <div className="--logo topLeft">
          <img
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/static/logo.png`}
            alt="logo"
            onClick={onClickLogo}
            style={{ width: '180px' }}
          />
        </div>
      )}
      {themeConfig?.selected === 'blank' &&
        !themeConfig?.config?.isHideInfoTour && (
          <div className="--info">
            <h1 className="--projectTitle">{detailProject?.title || '---'}</h1>
            {!!detailProject?.subTitle && (
              <p className="address">{detailProject?.subTitle}</p>
            )}

            <div className="popInfo">
              <button className="popInfoToggle" type="button">
                {sceneSelected?.title}
                {sceneSelected?.post && (
                  <span>
                    <i aria-hidden className="fas fa-info" />
                  </span>
                )}
              </button>
            </div>
          </div>
        )}
    </HeaderWrapper>
  );
});

export default Header;
